body {
	margin: 0;
	padding: 0;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans',
		'Droid Sans', 'Helvetica Neue', sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

ul.topnav {
	list-style-type: none;
	margin: 0;
	padding: 0;
	overflow: hidden;
	background-color: #333;
}
ul.topnav li {
	float: left;
}

ul.topnav li a {
	display: block;
	color: white;
	text-align: center;
	padding: 14px 16px;
	text-decoration: none;
}

ul.topnav li a:hover:not (.active) {
	background-color: #111;
}
ul.topnav li a.active {
	background-color: darkgrey;
}
ul.topnav li.right {
	float: right;
}

ul {
	list-style: none;
}
a {
	text-decoration: none;
	color: #0000ff;
}
input,
select,
textarea {
	width: 600px;
	padding: 6px 12px;
	margin: 8px 0;
	border-radius: 4px;
	box-sizing: border-box;
	border: 1px solid #ccc;
}

button {
	background-color: #4caf50;
	color: white;
	padding: 10px 20px;
	border: none;
	border-radius: 4px;
	cursor: pointer;
	margin-top: 20px;
	margin-bottom: 40px;
}

.content {
	text-align: center;
}

.form {
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
}

.Home {
	margin-top: 30px;
}

.card {
	box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
	max-width: 600px;
	margin: auto;
	text-align: center;
	margin-bottom: 35px;
}

.card-category {
	color: grey;
	font-size: 18px;
	text-transform: capitalize;
}

.card-button {
	border: none;
	outline: 0;
	display: inline-block;
	padding: 8px;
	color: white;
	background-color: #000;
	text-align: center;
	width: 100%;
	cursor: pointer;
	font-size: 18px;
	margin-top: 7px;
	margin-bottom: 0;
	border-radius: 0;
	text-decoration: none;
}

.card p {
	line-height: 0.5;
}

.center {
	text-align: center;
}

.story {
	width: 600px;
	margin: auto;
	margin-bottom: 50px;
}
.heart-icon {
	color: red;
}

.story-text {
	text-align: left;
}

.like-button {
	display: inline-block;
	width: 50px;
	padding: 4px 8px;
	border: none;
	color: white;
	background-color: #0000ff;
	border-radius: 3px;
	text-transform: uppercase;
	cursor: pointer;
}

.unlike-button {
	display: inline-block;
	width: 60px;
	border: none;
	background-color: #808000;
	color: white;
	padding: 4px 8px;
	border-radius: 3px;
	text-transform: uppercase;
	cursor: pointer;
}

.delete-button {
	margin-left: 10px;
	display: inline-block;
	width: 50px;
	background-color: #dc143c;
	padding: 4px 8px;
	color: white;
	border-radius: 3px;
	cursor: pointer;
}

.story-link {
	text-decoration: none;
	color: #0000ff;
}
@media screen and (max-width: 600px) {
	ul.topnav li.right,
	ul.topnav li {
		float: none;
	}
}
